ul {
  -webkit-padding-start: 0px;
}

.email-contact-container {
  text-align: center;
  font-family: "Alegreya Sans", sans-serif;
  color: white;
}
.email-contact-container h2 {
  font-size: 3em;
}

.email-links-ul {
  list-style: none;
  margin: auto;
  text-align: center;
  font-size: 20px;
  margin-bottom: 20px;
}

.email-links-ul a {
  font-size: 25px;
  color: white;
}

.email-form {
  display: grid;
  grid-template-columns: 1fr;
  grid-template-rows: 1fr 3fr 1fr;
  grid-gap: 0.5em;
  max-width: 715px;
  margin: auto;
}

.name-email-input {
  display: -ms-flexbox;
  display: flex;
  -ms-flex-pack: justify;
      justify-content: space-between;
}
.name-email-input input {
  border-radius: 4px;
  width: 48%;
  padding: 10px;
  margin: 2px;
  /* background: #4eb980; */
  border: solid 1px rgba(224, 224, 224, 0.75);
  color: white;
  font-size: 20px;
}

.name-email-input input:focus {
  background: rgba(224, 224, 224, 0.15);
  border: 1px solid white;
  outline: none;
}

::-webkit-input-placeholder {
  color: rgba(224, 224, 224, 0.75);
  opacity: 1;
}

:-ms-input-placeholder {
  color: rgba(224, 224, 224, 0.75);
  opacity: 1;
}

::-ms-input-placeholder {
  color: rgba(224, 224, 224, 0.75);
  opacity: 1;
}

::placeholder {
  color: rgba(224, 224, 224, 0.75);
  opacity: 1;
}

.textarea textarea {
  width: 99%;
  margin: 2px;
  padding: 10px;
  padding-right: 0px;
  border-radius: 4px;
  /* background: #4eb980; */
  border: solid 1px rgba(224, 224, 224, 0.75);
  height: 85%;
  color: white;
  font-size: 20px;
}

.textarea textarea:focus {
  background: rgba(224, 224, 224, 0.15);
  border: 1px solid white;
  outline: none;
}

.sumbit-container {
  -ms-flex-line-pack: center;
      align-content: center;
}

.submit {
  background: white;
  width: 99%;
  padding: 10px;
  margin: 2px;
  color: darkgreen;
  font-size: 20px;
  border-radius: 4px;
  border: 1px solid white;
  -webkit-transition: all 0.2s ease-in-out;
  -o-transition: all 0.2s ease-in-out;
  transition: all 0.2s ease-in-out;
}

.submit:hover {
  color: white;
  background: none;
  cursor: pointer;
}

.link-list {
  list-style: none;
  display: -ms-flexbox;
  display: flex;
  -ms-flex-pack: distribute;
      justify-content: space-around;
  font-size: 25px;
  margin: auto;
  margin-top: 25px;
  max-width: 715px;
}

.link-list a i {
  color: white;
  width: 45px;
  border: 1px solid white;
  border-radius: 50%;
  padding: 10px;
  text-align: center;
  -webkit-transition: all 0.2s ease-in-out;
  -o-transition: all 0.2s ease-in-out;
  transition: all 0.2s ease-in-out;
}

.link-list a i:hover {
  color: black;
  background: white;
}



