.menu {
  display: -ms-flexbox;
  display: flex;
  font-size: 16px;
  -ms-flex-align: center;
      align-items: center;
  -ms-flex-pack: justify;
      justify-content: space-between;
  min-width: 275px;
}

.menu-bars {
  padding: 25px;
  font-size: 40px;
}

.menu-bars:hover {
  cursor: pointer;
}

#navBar-container {
  z-index: 2;
}

.nav-container {
  position: fixed;
  width: 100%;
  top: 0;
  min-height: 100%;
  background: white;
  opacity: 0.93;
  font-family: "Stardos Stencil", cursive;
  font-size: 45px;
  z-index: 100;
  grid-template-rows: 1fr 5fr;
  display: none;
}

.close-nav {
  font-family: sans-serif;
  text-align: right;
}

.nav-close {
  color: black;
  -webkit-transition: all 0.2s ease-in-out;
  -o-transition: all 0.2s ease-in-out;
  transition: all 0.2s ease-in-out;
  display: inline-block;
}

.nav-close:hover {
  cursor: pointer;
  font-size: 55px;
}

.close-nav div {
  padding: 25px;
  font-size: 50px;
  height: 60px;
}

.nav-close:hover {
  cursor: pointer;
}

.resume-nav {
  font-family: "Stardos Stencil", cursive;
  font-size: 45px;
  color: black;
  text-decoration: none;
  display: inline-block;
}

.nav-ul {
  -ms-flex-pack: center;
      justify-content: center;
  -ms-flex-align: center;
      align-items: center;
  list-style: none;
  margin-left: 5%;
  -webkit-tap-highlight-color: rgba(255, 255, 255, 0);
}

.nav-ul li div:hover {
  cursor: pointer;
}

.nav-ul a:hover {
  cursor: pointer;
}

.slideInLeft {
  -webkit-animation-duration: 0.5s;
          animation-duration: 0.5s;
  -webkit-animation-name: leftSlideIn;
          animation-name: leftSlideIn;
}

.slideOutLeft {
  -webkit-animation-duration: 0.5s;
          animation-duration: 0.5s;
  -webkit-animation-name: leftSlideOut;
          animation-name: leftSlideOut;
}

@-webkit-keyframes leftSlideIn {
  0% {
    margin-left: -100%;
  }
  100% {
    margin-left: 0%;
  }
}

@keyframes leftSlideIn {
  0% {
    margin-left: -100%;
  }
  100% {
    margin-left: 0%;
  }
}

@-webkit-keyframes leftSlideOut {
  0% {
    margin-left: 0%;
  }
  100% {
    margin-left: -100%;
  }
}

@keyframes leftSlideOut {
  0% {
    margin-left: 0%;
  }
  100% {
    margin-left: -100%;
  }
}

.li-selector {
  display: inline;
  -webkit-transition: all 300ms ease-in-out;
  -o-transition: all 300ms ease-in-out;
  transition: all 300ms ease-in-out;
}

.li-selector:hover {
  font-size: 60px;
}

.li-selector a:hover {
  font-size: 60px;
}

.li-selector a {
  -webkit-transition: all 300ms ease-in-out;
  -o-transition: all 300ms ease-in-out;
  transition: all 300ms ease-in-out;
}

.project-header {
  font-family: "Stardos Stencil", cursive;
  font-size: 30px;
  margin-left: -70px;
  border-radius: 5px;
  padding: 10px;
}
.projects-header {
  background: rgba(255, 255, 255, 0.3);
}

.skill-header {
  color: white;
}

@media only screen and (min-width: 600px) {
  .skill-header {
    font-size: 50px;
  }
}



