html {
  background-image: image("../images/blackandwhite.jpg");
  -o-object-fit: cover;
     object-fit: cover;
}

.about-container {
  font-family: "Alegreya Sans", sans-serif;
  text-align: center;
  color: white;
  max-width: 820px;
  min-width: 375px;
  margin: auto;
}

.about-container h1 {
  font-weight: 100;
  font-size: 45px;
  font-family: "Stardos Stencil", cursive;
  text-shadow: 2px 2px 4px #000000;
}

.about-container h2 {
  margin-top: 40px;
  font-weight: bold;
  text-shadow: 2px 2px 4px #000000;
}

.about-container h3 {
  margin-bottom: 15px;
  text-shadow: 2px 2px 4px #000000;
}

.bio {
  margin: 15px;
  font-family: "Dosis", sans-serif;
  text-align: left;
  background-color: rgba(0, 0, 0, 0.5);
}

.about-section {
  display: grid;
  grid-template-columns: 1fr;
  grid-template-rows: 1fr;
}

.photo-carousel {
  max-width: 373px;
  height: 373px;
  -o-object-fit: cover;
     object-fit: cover;
}

.slick-list {
  max-width: 373px;
  max-height: 375px;
  border-radius: 10px;
  margin: auto;
}

.slick-slider {
  max-width: 475px;
  margin: auto;
}

.slick-next:before,
.slick-prev:before {
  font-size: 40px;
  line-height: 1;
  opacity: 0.75;
  color: white;
  display: none;
}

.slick-dots li.slick-active button:before {
  color: white;
}

.slick-slider .slick-initialized {
  display: -ms-flexbox;
  display: flex;
  -ms-flex-pack: distribute;
      justify-content: space-around;
}

.slick-dots li button:before {
  color: white;
}

.slick-next, .slick-prev {
  width: inherit;
  height: inherit;
}

@media only screen and (min-width: 620px) {
  .slick-next:before,
  .slick-prev:before {
    display: block;
  }
}



